.burger-btn {
  display: none;


  @media (max-width: $tablet) {
    display: block;
    position: relative;
    top: 0;
    right: 12px;
    z-index: 2;
    width: 50px;
    height: 50px;
    margin-right: auto;
    background: transparent;
    border: none;
    outline: none;

    &::before {
      content: "";
      position: absolute;
      top: 13%;
      left: 11px;
      width: 25px;
      height: 2px;
      background-position: 50% 50%;
      box-shadow: 0 8px 0 0 $basic-color, 0 16px 0 0 $basic-color, 0 24px 0 0 $basic-color;
    }
  }
}

.header--opened .burger-btn {
  display: none;

  @media (max-width: $tablet) {
    display: block;

    &::before {
      box-shadow: 0 8px 0 0 $basic-background, 0 16px 0 0 $basic-background, 0 24px 0 0 $basic-background;
    }
  }
}

.burger-btn--js:hover {
  cursor: pointer;
}
